body {
  background-color: black;
  overflow: hidden;
}

body, html {
  height: 100%;
}

#root {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  text-align: center;
  overflow: hidden;
}

#root .image-gallery {
  width: auto;
  height: 100%;
}

.image-gallery-slide-wrapper button {
  opacity: 0.0;
  transition: opacity 450ms;
}

.image-gallery-slide-wrapper:hover button {
  opacity: 1.0;
}
